import React, { useEffect, useState } from 'react';
import {Select, Avatar, List, Card, message, Button, Tooltip} from 'antd';
import {addItemToCart, getMenus, getRestaurants} from "../utils";
import { PlusOutlined } from "@ant-design/icons";

const { Option } = Select;

function FoodList() {
    const [restaurants, setRestaurants] = useState([]);
    const [loadingRest, setLoadingRest] = useState(false);
    const [curRest, setCurRest] = useState();
    const [foodData, setFoodData] = useState([]);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        setLoadingRest(true);
        getRestaurants()
            .then((data) => {
                setRestaurants(data);
            })
            .catch((err) => {
                message.error(err.message);
            })
            .finally(() => {
                setLoadingRest(false);
            })

    });

    useEffect(() => {
        if (curRest) {
            setLoading(true);
            getMenus(curRest)
                .then((data) => {
                    setFoodData(data);
                })
                .catch((err) => {
                    message.error(err.message);
                })
                .finally(() => {
                    setLoading(false);
                });
        }
    }, [curRest]);

    return (
        <>
            <Select
                value={curRest}
                onSelect={(value) => setCurRest(value)}
                placeholder="Select a restaurant"
            >
                {restaurants.map((item) => {
                    return <Option value={item.id}>{item.name}</Option>;
                })};
            </Select>

            <List
                style={{ marginTop: 20 }}
                loading={loading}
                grid={{
                    gutter: 16,
                    xs: 1,
                    sm: 2,
                    md: 4,
                    lg: 4,
                    xl: 3,
                    xxl: 3,
                }}
                dataSource={foodData}
                renderItem={(item) => (
                    <List.Item>
                        <Card
                            title={item.name}
                            extra={<AddToCartButton itemId={item.id} />}
                        >
                            <img
                                src={item.imageUrl}
                                alt={item.name}
                                style={{ height: 340, width: "100%", display: "block" }}
                            />
                            {`Price: ${item.price}`}
                        </Card>
                    </List.Item>
                )}
            />
        </>
    );
}

function AddToCartButton({itemId}) {
    const [loading, setLoading] = useState(false);

    const AddToCart = () => {
        setLoading(true);
        addItemToCart(itemId)
            .then(() => message.success(`Successfully add item`))
            .catch((err) => message.error(err.message))
            .finally(() => {
                setLoading(false);
            });
    };

    return (
        <Tooltip title="Add to shopping cart">
            <Button
                loading={loading}
                type="primary"
                icon={<PlusOutlined />}
                onClick={AddToCart}
            />
        </Tooltip>
    );
}

export default FoodList;

